<template>
    <vue-scroll>
        <div class="edit-modify"  v-loading="loading">
            <div class="content-modify">

                <el-form size="small" :model="modifyForm" label-width="100px" class="form-item" label-position="top">
                    <template v-if="type == 'text' || type == 'html' ">
                        <div class="item">
                            <div class="tit">
                                {{ $L('模块标题') }}：
                            </div>
                            <el-form-item prop="title"  class="form-item">
                                {{ modifyForm.title }}
                            </el-form-item>
                        </div>
                        <el-form-item :label="$L('模块内容')+'：'" prop="text" class="form-item-two">
                            <quill-editor class="editor" maxlength="50000"
                                          v-model="modifyForm.text"
                                          :options="editorOption">
                            </quill-editor>
                        </el-form-item>
                    </template>
                    <template v-else>
                        <div class="item">
                            <div class="tit">
                                {{ $L('模块标题') }}：
                            </div>
                            <el-form-item prop="title"  class="form-item">
                                {{ modifyForm.title }}
                            </el-form-item>
                        </div>
                        <div class="item">
                            <div class="tit">
                                {{ $L(' 选择上传') }}：
                            </div>
                            <el-form-item prop="img" class="form-item">
                                <label class="label">
                                    <input class="mainBgColor" type="button" id="btn" :value="$L('选择文件')"/>
                                    <input id="fileinp" type="file" name="CommentsPic" :placeholder="$L('上传图片')" multiple accept="image/png,image/jpeg,image/jpg" @change="upImgFunc"/>
                                    <div class="img-item">
                                        <img v-if="modifyForm.img === 'http://bbx11.nicebox.test' || modifyForm.img === '' "
                                             src="../../../assets/img/distributor/addpt.jpg" border="0" class="up-show" align="absmiddle">
                                        <img v-else :src="modifyForm.img" border="0" class="up-show" align="absmiddle">
                                    </div>

                                </label>
                            </el-form-item>
                        </div>

                    </template>
                </el-form>
            </div>
            <div class="bottom">
                <el-button size="mini" @click="backFunc">{{ $L('取消返回') }}</el-button>
                <el-button size="mini" @click="defaultFunc">{{ $L('恢复默认') }}</el-button>
                <el-button size="mini" @click="saveingFunc" type="primary">{{ $L('保存设置') }}</el-button>
            </div>
        </div>
    </vue-scroll>
</template>

<script>
    export default {
        name: "editModify",
        data() {
            return {
                loading: true,
                editorOption: {},
                type:0,         //判断是text，html,picture,image
                blockName:'',     //列表数据
                langId:'',
                pageId:'commHeader',
                modifyForm:{
                    title:'',
                    text:'',
                    img:''
                },
                routerData:{},
                uploadImg:[]
            }
        },
        created() {
            if(this.$route.query.hasOwnProperty('moduleType')){
                this.type = this.$route.query.moduleType;
            }
            if(this.$route.query.hasOwnProperty('idBlock')){
                this.idBlock = this.$route.query.idBlock;
            }
            if(this.$route.query.hasOwnProperty('blockName')){
                this.blockName = this.$route.query.blockName;
            }
            if(this.$route.query.hasOwnProperty('langId')){
                this.langId =  this.$route.query.langId;
            }
            if(this.$route.query.hasOwnProperty('pageId')){
                this.pageId =  this.$route.query.pageId;
            }

            this.getData();
        },
        methods: {
            getData() {
                const _self = this;
                _self.loading = true;
                let arr = {
                    langId:_self.langId,
                    pageId:_self.pageId,
                    blockName:_self.blockName
                };
                if(_self.$route.query.hasOwnProperty('idBlock')){
                    arr.idBlock = this.idBlock;
                };
                _self.$api('editBlockContent', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.routerData = reData.data;
                        if( _self.routerData.hasOwnProperty('text')){
                            _self.modifyForm.title = _self.routerData.text.title;
                            _self.modifyForm.text  = _self.routerData.text.text;
                        }
                        if( _self.routerData.hasOwnProperty('image')){
                            _self.modifyForm.title = _self.routerData.image.title;
                            _self.modifyForm.img = _self.routerData.image.file;
                        }

                        _self.loading = false;
                    } else if (reData.flag === 2) {
                        _self.$confirm(_self.$L('参数缺失'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.backFunc();
                        });
                    } else if (reData.flag === 11) {
                        _self.$confirm(_self.$L('模块不存在'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.backFunc();
                        });
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.backFunc();
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            //上传图片
            upImgFunc(event) {
                const _self = this;
                let files = event.target.files[0];
                let size = Math.floor(files.size / 1024);
                let type = files.type.substring(6);
                if (size > 4*1024*1024) {
                    _self.$Message.info('请选择4M以内的图片！');
                    return false;
                }
                if(!(type === 'png' || type === 'jpeg' || type === 'jpg')){
                    _self.$Message.info('请选择png、jpeg、jpg的图片格式！');
                    return false;
                }
                _self.uploadImg = {
                    'file':files
                };
                let reader = new FileReader();
                reader.readAsDataURL(files);
                reader.onload = function(evt){
                    _self.modifyForm.img = evt.target.result;
                }
            },
            backFunc() {
                this.$router.push({name:'distributorModifyMobile'});
            },
            defaultFunc(){
                const _self = this;
                if( _self.routerData.hasOwnProperty('text')){
                    _self.modifyForm.text  = _self.routerData.text.text;
                }
                if( _self.routerData.hasOwnProperty('image')){
                    _self.modifyForm.img = _self.routerData.image.file;
                }
            },
            saveingFunc(){
                const _self = this;
                _self.loading = true;
                setTimeout(()=>{
                    let array = {
                        langId: _self.langId,
                        pageId: _self.pageId,
                        blockName: _self.blockName
                    };
                    if( _self.routerData.hasOwnProperty('text')){
                        array.blockText =  _self.modifyForm.text;
                    };
                    if(_self.routerData.hasOwnProperty('image')) {
                        _self.modifyForm.image = _self.uploadImg;
                        array.PicFile = _self.modifyForm.image;
                    };
                    if(this.$route.query.hasOwnProperty('idBlock')){
                        array.idBlock = _self.idBlock;
                    };
                    let postData = _self.$objToFormData(array);
                    _self.$api('saveBlockContent', postData, function (reData) {
                        if (reData.flag === 1) {
                            _self.$message({
                                message: _self.$L('保存成功！'),
                                type: 'success'
                            });
                            setTimeout(()=>{
                                _self.loading = false;
                                typeof func === "function" && func();
                            },500);
                            _self.backFunc();
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else if (reData.flag === 2) {
                            _self.$confirm(_self.$L('参数缺失'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.saveingFunc();
                            }).catch(action => {
                                _self.backFunc();
                            });
                        } else if (reData.flag === 10) {
                            _self.$confirm(_self.$L('保存失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.saveingFunc();
                            }).catch(action => {
                                _self.backFunc();
                            });
                        } else if (reData.flag === 11) {
                            _self.$confirm(_self.$L('模块不存在'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.saveingFunc();
                            }).catch(action => {
                                _self.backFunc();
                            });
                        } else if (reData.flag === 13) {
                            _self.$confirm(_self.$L('上传图片失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.saveingFunc();
                            }).catch(action => {
                                _self.backFunc();
                            });
                        } else if (reData.flag === 14) {
                            _self.$confirm(_self.$L('图片大小不要超过4M'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.saveingFunc();
                            }).catch(action => {
                                _self.backFunc();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.saveingFunc();
                            }).catch(action => {
                                _self.backFunc();
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.saveingFunc();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    },true);

                },300);
            }
        }
    }
</script>

<style lang="scss" scoped>
    .edit-modify{
        position: relative;
        background: #fff !important;
        padding: 10px !important;
        .content-modify{
            .form-item{
                width: 100%;
                .item{
                    margin-bottom: 10px;
                }
                .editor {
                    position: relative;
                    width: 100%;
                    height: 400px;
                    display: inline-block;
                }
                .tit{
                    min-width: 130px;
                    color: #606266;
                    font-size: 14px;
                    line-height: 32px;
                    display: inline-block;
                    vertical-align: top;
                }
                .form-item{
                    width: calc(100% - 130px);
                    display: inline-block;
                    margin-bottom: 0;
                }
                .form-item-two{
                    margin-bottom: 0;
                }
                .label{
                    position: relative;
                    display: block;
                    cursor: pointer;
                    .mainBgColor{
                        color: #fff !important;
                    }
                    #fileinp{
                        position: absolute;
                        left: 0;
                        opacity: 0;
                        width: 90px;
                        height: 32px;
                        cursor: pointer;
                    }
                    #btn{
                        display: inline-block;
                        line-height: 1;
                        white-space: nowrap;
                        cursor: pointer;
                        background: #FFFFFF;
                        border: 1px solid #DCDFE6;
                        border-color: #DCDFE6;
                        color: #606266;
                        -webkit-appearance: none;
                        text-align: center;
                        -webkit-box-sizing: border-box;
                        box-sizing: border-box;
                        outline: none;
                        margin: 0;
                        -webkit-transition: .1s;
                        transition: .1s;
                        font-weight: 500;
                        -moz-user-select: none;
                        -webkit-user-select: none;
                        -ms-user-select: none;
                        padding: 9px 20px;
                        font-size: 12px;
                        border-radius: 0px;
                    }
                }
                .img-item {
                    margin-top: 20px;
                    .img-input {
                        width: 200px;
                        height: 200px;
                        position: absolute;
                        opacity: 0;
                        left: 0;
                        top: 0;
                        z-index: 9999;
                        cursor: pointer;
                    }
                    .up-show {
                        height: 100px;
                        width: 100px;
                        cursor: auto;
                        border: 1px solid #DBDBDB;
                    }
                }
            }
        }
        .bottom{
            text-align: right;
            margin: 10px auto;
        }
    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
    @media screen and (max-width:767px) {
        /deep/ .ql-formats {
            margin-right: 0 !important;
            float: left !important;
        }
    }
</style>